.TabNav {
	position: relative;
	width: 100%;
	height: 100%;

	& ul {
		display: flex;
		padding-bottom: 1rem;
	}
}

.link {
	display: flex;
	width: 80%;
	height: 5rem;

	margin-bottom: 1rem;

	transition: all 0.3s ease-in;
	& img {
		width: 2rem;
		margin-right: 1rem;
	}

	&.active {
		transition: all 0.3s ease-in;
		width: 20%;
	}
}

.button {
	border: none;
	outline: none;
	width: 100%;
	height: 100%;

	text-align: left;
	text-transform: uppercase;

	background: #ffffff;

	cursor: pointer;
	&.active {
		background: #edf0f1;
		color: #179fa6;
	}
}

.wrap {
	height: 100%;
	background-color: #ffffff;
	//!
	background: #d60909;
}

.tabwrap {
	position: relative;
	display: flex;
	justify-content: center;

	width: 100%;
	height: 100%;
}
